.form {
    margin: 2em 0;
    padding: 2em 4em;
    width: 40em;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: hsl(261, 54%, 97%);
    border: 3px solid hsl(261, 54%, 70%);
    border-radius: 1em;
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.6);
}

.title {
    margin: 0 0 1em;
    color: hsl(261, 54%, 25%);
    text-shadow: 1px 1px 2px hsl(261, 54%, 70%);
}

.form__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}

.form__question {
    color: hsl(261, 54%, 25%);
    font-size: 1.4em;
    font-weight: 600;
    margin-left: 1.5em;
}

.form__input {
    color: #363636;
    width: 80%;
    height: 1.5rem;
    font-size: 1.2em;
    outline: none;
    border: 1px solid #666;
    border-radius: 0.4rem;
    padding: 0.2em 0.4em;
    margin: 0 auto 3em auto;
}

.form__select {
    color: #444;
    width: 83%;
    height: 1.7em;
    outline: none;
    border: 1px solid #666;
    border-radius: 0.4rem;
    padding: 0.2em 0.4em;
    font-size: 1.2em;
    margin: 0 auto 3em auto;
}

.select__option {
    height: 1.5em;
    font-size: 1em;
}

.form__submit {
    width: 10em;
    font-size: 1.2em;
    margin: 1em 0;
    padding: 0.5em 0.6em;
    border-radius: 0.2em;
    border: 2px solid hsl(261, 54%, 70%);
    border-radius: 0.5em;
    color: hsl(261, 54%, 25%);
    cursor: pointer;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
    transition: all ease-in 0.3s;
}

.form__submit:hover:enabled {
    background-color: hsl(261, 54%, 80%);
}

.form__submit:active:enabled {
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
}

.form__submit:disabled {
    border: 2px solid hsl(261, 54%, 80%);
    color: #aaa;
    cursor: not-allowed;
}

.form__divisor {
    background-color: hsl(261, 54%, 70%);
    width: 100%;
    height: 3px;
    border-radius: 2px;
}

@media (width < 800px) {
    .form {
        width: 30em;
    }
}

@media (width < 650px) {
    .form {
        width: 80vw;
        padding: 2em;
    }
}

@media (width < 500px) {
    .form {
        width: 80vw;
        padding: 1em;
        text-align: center;
    }
    .form__question {
        margin: 0;
    }
    .form__input {
        width: 95%;
    }
}